<template>
	<div id="news-notice">
		<public-header title="公告列表"></public-header>
		<div class="content">
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="notice-list"
			>
				<div class="notice-item border-1px-b padding-10" v-for="(item, index) in records.data">
					<router-link :to="util.getUrl({path: '/pages/news/detail', query:{id: item.id}})">
						<div class="item-left">
							<div class="itag" :class="{'itag-info': item.is_new == 1, 'itag-disabled': item.is_new == 0}">消息</div>
						</div>
						<div class="item-right">
							<div class="font-16 ellipsis c-default">{{item.title}}</div>
							<div class="font-14 ellipsis c-default">{{item.addtime}}</div>
						</div>
					</router-link>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>暂无数据!</p>
			</div>
			</van-pull-refresh>
			<iloading v-if="showPreLoading"></iloading>
		</div>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'manage/news/notice/list',
					recordsName: 'notice'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#news-notice .content .notice-item{
		overflow: hidden;
		background: #fff;
	}
	#news-notice .content .notice-item .item-left{
		float: left;
		width:15%;
		text-align:right;
	}
	#news-notice .content .notice-item .item-right{
		float: left;
		padding-left: 10px;
		width:85%;
	}
	#news-notice .content .notice-item .item-right div:last-child{
		padding-top: 10px;
	}
</style>


